@import url(https://fonts.googleapis.com/css2?family=Electrolize&family=Fira+Code:wght@300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@charset 'utf-8';

:root {
	--color-black: #000;
	--color-blue: #3F84E5;
	--color-blueDarken: #4361EE;
	--color-gray: #909090;
	--color-grayDark: #2D2D2D;
	--color-grayDarken: #1a1a1b;
	--color-highlight-green: #04d361;
	--color-highlight-greenDarken: #03b955;
	--color-highlight-yellow: #ffef5c;
	--color-primary: #7209b7;
	--color-secondary: #f0f0f7;
	--color-purpleDarken: #3a0ca3;
	--color-logo: #bf60ff;
	--color-red: #FF4B4B;
	--color-redDarken: #FF3232;
	--color-white: #fff;
	
	font-size: 60%;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

	scrollbar-color: #909090 #1a1a1b;

	scrollbar-color: var(--color-gray) var(--color-grayDarken);
	scroll-behavior: smooth;
}

html,
body,
#root {
	height: 100vh;

	cursor: default;
}

body {
	background: #1a1a1b;
	background: var(--color-grayDarken);
	--bodyPaddingTop: 10vh;
}

#root {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	min-height: calc(100vh - var(--bodyPaddingTop));
}

body,
input,
button,
textarea {
	font: 500 1.6rem Roboto;
}

.container {
	width: 100%;
	max-width: 100vw;
	height: 100vh;
}

@media (min-width: 700px) {
	:root {
		font-size: 62.5%;
	}
}

main {
	width: 100%;
	max-width: 100%;

	-webkit-scroll-snap-type: y mandatory;

	    -ms-scroll-snap-type: y mandatory;

	        scroll-snap-type: y mandatory;
}

header {
	position: fixed;
	width: 100%;
	height: 10vh;
	left: 0px;
	top: 0px;
	z-index: 150;

	padding: 0 5%;

	display: flex;
	justify-content: space-between;
	align-items: center;

	background: var(--color-black);
	border-bottom: 2px solid var(--color-primary);
	box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 3px 1px;
}

header a {
	color: white;
}

header .logo {
	position: relative;
	font-family: 'Electrolize', sans-serif;
	font-size: 24px;
	font-weight: 500;
	font-style: italic;

	cursor: pointer;
}

header .logo b {
	color: var(--color-logo);
}

header .logo img {
	position: absolute;
	top: 2px;
	width: 30px;

	transform: rotate(5deg);
}

header nav ul {
	display: flex;
	justify-content: space-between;
	align-items: center;

	list-style: none;

	font-family: 'Roboto', sans-serif;
}

header nav ul li {
	letter-spacing: 2px;

	margin-left: 10px;
}

header nav ul li:not(:last-child) {
	margin-right: 10px;
}

header nav ul li a {
	position: relative;

	text-decoration: none;
	cursor: pointer;

	display: flex;
	align-items: center;
	justify-content: center;
}

header nav ul li a:after {
	content: '';
	position: absolute;
	left: 50%;
	bottom: -10px;
	transform: translateX(-50%) scaleX(0);
	transform-origin: 50% 50%;
	width: 100%;
	height: 4px;
	background: var(--color-purpleDarken);
	transition: all 100ms;
	opacity: 0;
	border-radius: 5px;
}

header nav ul li a:hover:after {
	transform: translateX(-50%) scaleX(1);
	opacity: 1;
}

header nav ul li > .active:after {
	transform: translateX(-50%) scaleX(1);
	opacity: 1;

	background: var(--color-primary);
}

.scroll-down {
	width: 100%;
	height: 100%;

	display: flex;
	align-items: center;
  justify-content: flex-end;
	flex-direction: column;
	
	color: var(--color-gray);
	transition: color .3s;

	margin-bottom: 5px;
}

.scroll-down .arrow-content {
	display: flex;
	align-items: center;
	flex-direction: column;

	cursor: pointer;
}

.scroll-down .arrow-content span {
	font-weight: 300;
	text-align: center;

  position: absolute;
	margin-bottom: 75px;
	transition: color .3s;

	text-shadow: 0px 4px 10px rgba(0, 0, 0, 1);
}

.scroll-down .arrow-content .arrow-down {
	font-size: 94px;

	-webkit-animation: pulse 2s infinite;

	        animation: pulse 2s infinite;
	transition: color .3s;

	-webkit-filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 1));

	        filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 1));
}

.scroll-down .arrow-content:hover span {
	color: var(--color-secondary);
}

.scroll-down .arrow-content:hover .arrow-down {
	color: #944dd3;
}

@-webkit-keyframes pulse {
	0% {
		transform: translate(0, 0);
	}
	50% {
		transform: translate(0, 10px);
	}
	100% {
		transform: translate(0, 0);
	}
}

@keyframes pulse {
	0% {
		transform: translate(0, 0);
	}
	50% {
		transform: translate(0, 10px);
	}
	100% {
		transform: translate(0, 0);
	}
}

