.section-inscricao .card-inscricao form .input-email .email-icon {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 10%;

	background: var(--color-gray);

	border: 1px solid var(--color-gray);
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
}

.section-inscricao .card-inscricao form .input-email label {
	width: 100%;

	border-left: 1px solid var(--color-grayDark);
}

.section-inscricao .card-inscricao form input {
	background: #53585d;
	color: #f5f5f5;
	display: block;
	width: 100%;
	height: 57px;
	font-size: 18px;

	outline: 0;
	border: 1px solid #53585d;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;

	padding: 16px;

	resize: none;

	transition: all 0.3s;
}

.section-inscricao .card-inscricao form input:focus {
	background: #4d5257;
	border: 1px solid #4d5257;
}

.section-inscricao .card-inscricao form button {
	width: 100%;

	padding: 16px;

	border: 1px solid var(--color-highlight-green);
	border-radius: 5px;
	outline: none;

	background: var(--color-highlight-green);

	font-size: 20px;
	font-weight: 500;
	color: var(--color-secondary);
	text-transform: uppercase;

	display: inline-block;

	cursor: pointer;
	text-decoration: none;
	text-align: center;

	transition: background-color 0.3s;
}

.section-inscricao .card-inscricao form button:hover {
	background: var(--color-highlight-greenDarken);

	border: 1px solid var(--color-highlight-greenDarken);
}

footer {
	color: white;

	background: var(--color-black);
	border-top: 2px solid var(--color-primary);
	box-shadow: rgba(0, 0, 0, 0.3) 0px -1px 3px 1px;
  
	padding: 1% 5% 1%;
	
	display: flex;
	flex-direction: column;
}

footer .footer-content {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

footer .footer-info {
	display: flex;
	align-items: center;
	justify-content: center;

	height: 100%;

	font-size: 16px;
	font-weight: 400;

	color: var(--color-gray);
}

footer .footer-info a {
	text-decoration: none;
	color: var(--color-gray);

	font-weight: 700;
}

footer .logo {
	position: relative;
	font-family: 'Electrolize', sans-serif;
	font-size: 24px;
	font-weight: 500;
	font-style: italic;

	cursor: pointer;
}

footer .logo b {
	color: var(--color-logo);
}

footer .logo img {
	position: absolute;
	top: 2px;
	width: 30px;

	transform: rotate(5deg);
}

